import FeaturedProducts from "./FeaturedProducts";
import FeaturedBrands from "./FeaturedBrands";
import SubcategorySlide from "./SubcategorySlide";
import LeaderBoard from "../../components/LeaderBoard";
import { useParams } from "react-router-dom";
import HeroBanner from "../../components/HeroBanner";
import SquareBanner from "../../components/SquareBanners";
import useCategory from "../../hooks/useCategory";
import LargeRectangle from "../../components/LargeRectangle";
import Microbar from "../../components/Microbar";
import ProductList from "./ProductList";

const Category = () => {
  const { categoryId } = useParams();
  const { category, loading } = useCategory(categoryId);

  if (loading) {
    return <></>;
  }

  return (
    <section className="space-y-8 py-8">
      <HeroBanner position="Top" resourceType="Category" valueId={categoryId} />
      <SquareBanner
        position="Middle_1"
        resourceType="Category"
        valueId={categoryId}
        title={`Best of ${category?.name}`}
      />
      <FeaturedProducts />
      <LeaderBoard
        position="Middle_1"
        resourceType="Category"
        valueId={categoryId}
      />
      <HeroBanner
        position="Middle_1"
        resourceType="Category"
        valueId={categoryId}
      />
      <FeaturedBrands />
      <SubcategorySlide />
      <LargeRectangle
        position="Middle_1"
        resourceType="Category"
        valueId={categoryId}
      />
      <Microbar
        position="Middle_2"
        resourceType="Category"
        valueId={categoryId}
      />
      <LeaderBoard
        position="Middle_2"
        resourceType="Category"
        valueId={categoryId}
        title="Discover Your Ideal Choice"
        subtitle="Picked Just For You"
      />
      <ProductList />
    </section>
  );
};

export default Category;
