import { useParams } from "react-router-dom";
import { useVariants } from "../../services/productService";
import Carousel from "../../components/Carousel";
import useMediaQuery from "../../hooks/useMediaQuery";
import ProductCard from "../../components/ProductCard";
import Loader from "../../components/Loader";

const ProductList = () => {
  const { categoryId } = useParams();
  const { data, isLoading, error } = useVariants({
    page: 1,
    limit: 10,
    mainCategoryId: categoryId,
  });
  const maxXl = useMediaQuery("(max-width: 1280px)");
  const minMd = useMediaQuery("(min-width: 768px)");

  if (isLoading || (!error && !data)) {
    return <Loader />;
  }

  if (error) {
    return (
      <div className="w-full">
        <h2 className="text-center font-secondary font-medium text-xl mt-8">
          {error.message}
        </h2>
      </div>
    );
  }

  return (
    data?.variants?.length > 0 && (
      <div className="container max-w-full space-y-8 max-lg:space-y-4">
        {/* <h1 className="text-2xl max-lg:text-xl font-bold">Featured Products</h1> */}

        <Carousel
          slideWidth={!minMd ? 160 : 200}
          spaceBetween={maxXl ? 10 : 14}
          allowTouchMove
          navigation
        >
          {data?.variants?.map((product, index) => (
            <ProductCard key={index} product={product} />
          ))}
        </Carousel>
      </div>
    )
  );
};

export default ProductList;
