import { useParams } from "react-router-dom";
import { useFeaturedVariantsBasedOnCategory } from "../../services/productService";
import Carousel from "../../components/Carousel";
import useMediaQuery from "../../hooks/useMediaQuery";
import ProductCard from "../../components/ProductCard";

const FeaturedProducts = () => {
  const { categoryId } = useParams();
  const { data: featuredVariants, isLoading } =
    useFeaturedVariantsBasedOnCategory(categoryId);
  const maxXl = useMediaQuery("(max-width: 1280px)");
  const minMd = useMediaQuery("(min-width: 768px)");

  if (isLoading) {
    return <></>;
  }

  return (
    featuredVariants.length > 0 && (
      <div className="container max-w-full space-y-8 max-lg:space-y-4">
        <h1 className="text-2xl max-lg:text-xl font-bold">Featured Products</h1>

        <Carousel
          slideWidth={!minMd ? 160 : 200}
          spaceBetween={maxXl ? 10 : 14}
          allowTouchMove
          navigation
        >
          {featuredVariants.map((product, index) => (
            <ProductCard key={index} product={product} />
          ))}
        </Carousel>
      </div>
    )
  );
};

export default FeaturedProducts;
